.double__luxury__room {
  margin-bottom: 32px;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: calc(40 / 32);
  letter-spacing: -0.02em;
  color: var(--text-color-primary);
}

/* tablet screen */
@media screen and (min-width: 768px) {
  .double__luxury__room {
    margin-top: 40px;

    font-size: 40px;
    line-height: calc(44 / 40);
  }
}

/* desctop screen */
@media screen and (min-width: 1440px) {
  .double__luxury__room {
    margin-top: 50px;
  }
}

/* tablet screen */
@media screen and (min-width: 768px) {
  .double__room__list {
    display: flex;
    gap: 32px;
  }
}

/* tablet screen */
@media screen and (min-width: 768px) {
  .double__room__item__1__1 {
    max-width: 369px;
  }
}
/* desctop screen */
@media screen and (min-width: 1440px) {
  .double__room__item__1__1 {
    max-width: 355px;
  }
}

.double__room__text {
  text-transform: uppercase;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: calc(18 / 14);
  letter-spacing: -0.02em;
  color: var(--text-color-primary);
}
/* desctop screen */
@media screen and (min-width: 768px) {
  .double__room__text {
    font-size: 18px;
    line-height: calc(24 / 18);
  }
}
/* phone screen */
.double__room__text__seater {
  text-transform: uppercase;
  font-family: var(--font-primary);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}

/* tablet screen */
@media screen and (min-width: 768px) {
  .double__room__text__seater {
    font-size: 18px;
    line-height: calc(24 / 18);
  }
}
/* desctop screen */
@media screen and (min-width: 1440px) {
  .double__room__text__seater {
    width: 475px;
  }
}
.double__room__item__2__1 {
  margin-top: 24px;
}
/* tablet screen */
@media screen and (min-width: 768px) {
  .double__room__item__2__1 {
    margin-top: 0;
    max-width: 299px;
  }
}
.btn__more__details {
  text-align: center;
  font-family: var(--font-primary);
  letter-spacing: -0.02em;
  color: var(--text-color-primary);
  border: 0.8px solid var(--text-color-primary);
  text-transform: uppercase;
  background-color: #0000;
  border-radius: 32px;
  padding: 8px 18px;
  margin-top: 28px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

/* desctop screen */
@media screen and (min-width: 1440px) {
  .btn__more__details {
    max-height: 42px;
    min-width: 152px;
  }
}

.btn__more__details:hover {
  background-color: #121212;
  border: 0.8px solid #f7f7f7;
  border-radius: 32px;
  color: #f7f7f7;
}

.btn__more__details:focus {
  background-color: #121212;
  border: 0.8px solid #f7f7f7;
  box-shadow: -14px 24px 68px -16px rgba(0, 0, 0, 0.12);
  border-radius: 32px;
  color: #f7f7f7;
}

