.rest__hero {
  background-color: #f6f6f6;
  overflow: hidden;
}

.restaurant-hero-title {
  font-weight: 400;
  font-size: 40px;
  line-height: 1;

  letter-spacing: -0.02em;
  text-transform: uppercase;

  color: var(--text-color-primary);

  margin-bottom: 24px;
}

.restaurant-hero-btn-wrap {
  display: flex;
  gap: 12px;
  margin-bottom: 40px;
}

.restaurant-hero-btn {
  width: 128px;
  height: 34px;

  border: 0.8px solid #121212;
  border-radius: 32px;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: -0.02em;
  text-transform: uppercase;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: transparent;

  transition: color var(--animation-for-hover),
    background-color var(--animation-for-hover);
}

.restaurant-hero-btn:hover,
.restaurant-hero-btn:focus {
  background-color: var(--bg-color-dark);
  color: var(--bg-color-fone);
}

.hero__list {
  display: flex;
  gap: 20px;
}

.hero__img {
  max-width: 250px;
}

.restaurant-hero-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: 0.05em;
  text-transform: uppercase;

  margin-top: 40px;
}

@media screen and (max-width: 767px) {
  .restaurant-hero-text {
    margin-top: 100px;
  }
}

.restaurant-hero-text span {
  font-style: italic;
}

@media screen and (min-width: 375px) {
  .container-restaurant-hero {
    width: 375px;
    padding-left: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container-restaurant-hero {
    width: 768px;
    padding-left: 34px;
  }

  .restaurant-hero-title {
    margin-bottom: 32px;
    font-size: 80px;
  }

  .restaurant-hero-btn {
    width: 140px;
    height: 40px;
    line-height: 1.714;
  }

  .restaurant-herobuttons {
    gap: 10px;
  }

  .restaurant-hero-text {
    width: 338px;
  }
}

@media screen and (min-width: 1440px) {
  .rest-banner {
    display: flex;
    align-items: center;
  }
  .rest-wrap {
    margin-right: 181px;
  }
  .restaurant-hero-title {
    font-size: 90px;
    margin-bottom: 32px;
  }
  .restaurant-herobuttons {
    gap: 14px;
  }
  .restaurant-hero-btn {
    width: 167px;
    height: 42px;

    font-size: 18px;
    line-height: 1.333;
  }
  .restaurant-hero-text {
    margin-top: -38px;
  }
}
